.movie {
  border-bottom: 1px solid #eee;
  padding: 14px;
}
.movie:nth-child(odd) {
  background: #f9f9f9;
}
.movie__header {
  display: -ms-flexbox;
  display: flex;
}
.movie__img {
  -ms-flex: 1 1;
      flex: 1 1;
}
.movie__img img {
  max-width: 100%;
}
.movie__details {
  -ms-flex: 3 1;
      flex: 3 1;
  padding: 0 14px;
}
.movie__score {
  font-size: 26px;
}
.movie__title {
  font-size: 20px;
  margin-bottom: 8px;
}
.movie__genre {
  font-size: 14px;
}
.movie__time {
  display: -ms-flexbox;
  display: flex;
}
.movie__time > div {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 5px;
  font-weight: bold;
}
.movie__time > div:first-child {
  text-align: right;
  font-weight: normal;
}



