.app {
    /*max-width: 400px;*/
    /*margin: 0 auto;*/
    padding: 10px 10px;
}
.grid {
    display: grid;
    margin: 0 auto;
    grid-template-columns: repeat(3, 400px);
    grid-gap: 10px;
}
/* TABLE */
.table {
    padding: 10px;
    margin: 10px auto;
}
.table--collapse {
    border-collapse: collapse;
}
.table--small {
    font-size: 14px;
}
.table th, .table td {
    padding: 5px;
}
/* ALIGNMENT */
.text-right {
    text-align: right;
}
.text-center {
    text-align: center;
}
.text-small {
    font-size: 14px;
}
.no-wrap {
    white-space: nowrap;
}

/* WARN */
@-webkit-keyframes warn {
    0% { background-color: hsla(216, 97%, 100%, 1) }
    100% { background-color: hsla(216, 97%, 96%, 1) }
}
@keyframes warn {
    0% { background-color: hsla(216, 97%, 100%, 1) }
    100% { background-color: hsla(216, 97%, 96%, 1) }
}
.warn {
    -webkit-animation: warn 1.5s infinite alternate;
            animation: warn 1.5s infinite alternate;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}



